import React from 'react';
import Footer from "./Footer";
import Menu1 from "./Menu1";
import Menu2 from "./Menu2";
import Menu3 from "./Menu3";

import '../css/layout.css';

class MainComp extends React.Component{

	constructor(props){
		super();
		
		this.state = {
			Menu:0,
		}
	}
	
	changeMenu = (menuIndex)=>{
		this.setState({menu:menuIndex});
	}
	
	

	render(){
		return(
			<div>
				<div className="wrap">
					<div className="menuBar">
						<ul className="tabs">
							<li className={`${this.state.menu === 0? 'active': ''}`} onClick={() => this.changeMenu(0)}>Menu 1</li>
							<li className={`${this.state.menu === 1? 'active': ''}`} onClick={() => this.changeMenu(1)}>Menu 2</li>
							<li className={`${this.state.menu === 2? 'active': ''}`} onClick={() => this.changeMenu(2)}>Menu 3</li>
						</ul>
					</div>
				<div className="contentArea">
					{menuList[this.state.menu]}
				</div>
			</div>
			
			<Footer />
			</div>
		);
	}
}

const menuList = {
  0: <Menu1 />,
  1: <Menu2 />,
  2: <Menu3 />,
};
	
export default MainComp;
