import React from 'react';
import Button from '@material-ui/core/Button';

class Menu1 extends React.Component{
	
	movePage(e){
		console.log(e);
		
		if(e === 'btn1'){
			console.log("btn1 push");
		}else if(e === 'btn2'){
			console.log("btn2 push");
		}else{
			console.log("btn3 push");
		}
	}
	
	render(){
		return (
			<div>
				<Button name="btn1" variant="contained" onClick={() => this.movePage("btn1")}>Menu 1-1</Button>
				<Button name="btn2" variant="contained" onClick={() => this.movePage("btn2")}>Menu 1-2</Button>
				<Button name="btn3" variant="contained" onClick={() => this.movePage("btn3")}>Menu 1-3</Button>
			</div>
		);
	}
}

export default Menu1;
